import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { SpecializationDetailPage } from '../index';
import { SpecializationMapper } from '../../models/specialization.model';
import { SpecializationsProvider } from '../../providers/specializations/specializations';

/**
 * Generated class for the SpecializationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-specializations',
  templateUrl: 'specializations.html',
})
export class SpecializationsPage {

  public searching: boolean = false;

  public currentItems: SpecializationMapper[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private specializationsPrv: SpecializationsProvider, private modalCtrl: ModalController) {
    this.getItems();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpecializationsPage');
  }

  getItems() {
    this.searching = true;
    this.specializationsPrv.query().subscribe((data: SpecializationMapper[]) => {
      this.searching = false;
      this.currentItems = data;
    });
  }

  addItem() {
    let specializationDetailModal = this.modalCtrl.create(SpecializationDetailPage);
    specializationDetailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems()
      }
    });
    specializationDetailModal.present();
  }

  openItem(item: SpecializationMapper) {
    let specializationDetailModal = this.modalCtrl.create(SpecializationDetailPage, { item: item });
    specializationDetailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems();
      }
    });
    specializationDetailModal.present();
  }

}



